
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
  components: {
    XButton,
    Checklist,
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    editor (id) {
      let $router = this.$router
      if (id) {
        $router.push(`address/${id}`)
      } else {
        $router.push('address/0')
      }
    }
  },
  data () {
    return {
      title: '',
      list: [{
        id: 1,
        date: '2017-5-12 8:00',
        status: '小票审核通过',
        content: '小票兑换成功+20积分，目前积分520分'
      },
      {
        id: 2,
        date: '2017-5-12 8:00',
        status: '小票审核通过',
        content: '小票兑换成功+20积分，目前积分520分'
      }]
    }
  }
}



