import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


export default {
  components: {
    XInput,
    XButton,
    Group,
    Cell,
    Icon,
    Selector,
    Datetime
  },
  data () {
    return {
      title: '',
      header: '/static/head.jpeg',
      defaultValue: '',
      now: '2015-11-12',
      sex: [{key: '1', value: '男'}, {key: '0', value: '女'}]
